.container-front-image {
    position: relative;
    text-align: center;
    color: #32919e;
    font-weight: 800;
}

.container-front-image img {
    width: 100%;
    height: 450px;
    object-fit: cover;
    object-position: center;
}

.caption {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.mapouter {
    position: relative;
    text-align: right;
    height: 500px;
    width: 600px;
}

.gmap_canvas {
    overflow: hidden;
    background: none !important;
    height: 500px;
    width: 600px;
}
